<template>
  <el-container>
    <el-main>
      <el-row>
        <el-col :span="3">
          <el-tag class="me-2" size="large" type="danger">Total Book: {{totalCount}}</el-tag>
        </el-col>
        <el-col :span="6">
          <el-autocomplete
            ref="searchfield"
            v-model="searchValue"
            :fetch-suggestions="searchBook"
            :trigger-on-focus="false"
            :highlight-first-item="true"
            placeholder="Search..."
            :prefix-icon="Search"
            @select="selectBook"
            value-key="name"
            style="width:95%;"
            clearable
          />
        </el-col>
        <el-col :span="2">
          <el-input
            v-model.number="tempValue"
            @keydown.enter="handleAdd(selectedOption)"
            placeholder="00"
          >
            <template #append>
              <el-button @click="handleAdd(selectedOption)" :icon="Check"></el-button>
            </template>
          </el-input>
        </el-col>

        <el-col :span="13">
          <el-tooltip placement="left">
            <template #content>
              Left Click to Add
              <br />Right Click to Remove
            </template>
            <el-button @click="addColumn" @contextmenu.prevent="removeColumn" type="info">ADD COLUMN</el-button>
          </el-tooltip>
          <el-button @click="refreshTable" type="primary">REFRESH</el-button>
          <el-button @click="dialogFormVisible=true" type="success">ADD NEW BOOK</el-button>
          <el-button @click="toggleDialog" type="danger">PRINT</el-button>
          <el-button @click="reportDialog=true" type="warning" :icon="Setting" circle />
        </el-col>
      </el-row>
      <el-divider></el-divider>

      <el-row :key="refreshKey">
        <el-col v-for="m in column" :span="24/column" :key="m">
          <el-table :data="getdata(m)" border style="width: 100%">
            <el-table-column prop="name" label="Name">
              <template #default="scope">
                <el-button type="text" @click="scope.row.amount++">{{scope.row.name}}</el-button>
              </template>
            </el-table-column>
            <el-table-column prop="amount" label="Amount" width="120" class-name="no-padding">
              <template #default="scope">
                <el-input type="number" placeholder="100" v-model.number="scope.row.amount">
                  <template #append>
                    <el-popover placement="top" :width="40" trigger="click">
                      <template #reference>
                        <el-button tabindex="-1" :icon="Plus"></el-button>
                      </template>
                      <el-button-group size="small" style="margin-bottom:8px;">
                        <el-button
                          @click="handleCustomAdd(k*5, scope.row)"
                          v-for="k in 4"
                          :key="k"
                        >{{k*5}}</el-button>
                      </el-button-group>
                      <el-input
                        type="number"
                        placeholder="100"
                        :autofocus="true"
                        v-focus
                        v-model.number="tempValue"
                        @keyup.enter="handleAdd(scope.row)"
                      >
                        <template #append>
                          <el-button @click="handleAdd(scope.row)" :icon="Check"></el-button>
                        </template>
                      </el-input>
                    </el-popover>
                  </template>
                </el-input>
              </template>
            </el-table-column>
          </el-table>
        </el-col>
      </el-row>
    </el-main>

    <el-dialog v-model="dialogVisible" title="মাসিক হরেকৃষ্ণ সমাচার">
      <div id="printable">
        <h4 contenteditable="true" style="text-align:center">জানুয়ারি ২০২২</h4>
        <el-table show-summary border :data="printingData" sum-text="মোট">
          <el-table-column type="index" width="60" label="নং" />
          <el-table-column property="name" label="নাম"></el-table-column>
          <el-table-column property="amount" label="পরিমাণ" width="100"></el-table-column>
        </el-table>
      </div>
      <el-button-group class="btn-group">
        <el-button :icon="Printer" type="primary" plain @click="printMe"></el-button>
        <el-button :icon="DocumentCopy" type="success" plain @click="dialogFormVisible2=true"></el-button>
      </el-button-group>
    </el-dialog>

    <el-dialog v-model="dialogFormVisible" title="Add New Book" width="35%">
      <el-form :model="form" label-width="100px">
        <el-form-item label="Date">
          <el-col :span="11">
            <el-date-picker
              v-model="form.created_at"
              type="date"
              placeholder="Pick a date"
              style="width: 100%"
            />
          </el-col>
          <el-col :span="2" class="text-center">
            <span class="text-gray-500">-</span>
          </el-col>
          <el-col :span="11">
            <el-select v-model="form.category_id" placeholder="Category">
              <el-option label="HKP" value="1" />
              <el-option label="BBT" value="2" />
              <el-option label="Others" value="3" />
            </el-select>
          </el-col>
        </el-form-item>
        <el-form-item label="Bangla Name">
          <el-input v-model="form.name" placeholder="বইয়ের নাম (বাংলায়)" />
        </el-form-item>
        <el-form-item label="English Name">
          <el-input v-model="form.en_name" placeholder="Type Book Name" />
        </el-form-item>
        <el-form-item label="Price">
          <el-col :span="11">
            <el-input v-model.number="form.wholesale_price" placeholder="Wholesale" />
          </el-col>
          <el-col :span="2" class="text-center">
            <span class="text-gray-500">-</span>
          </el-col>
          <el-col :span="11">
            <el-input v-model.number="form.retail_price" placeholder="Retail" />
          </el-col>
        </el-form-item>
      </el-form>
      <template #footer>
        <span class="dialog-footer">
          <el-button @click="dialogFormVisible = false">Cancel</el-button>
          <el-button type="primary" :loading="formSubmiting" @click="addNewBook">Save</el-button>
        </span>
      </template>
    </el-dialog>

    <el-dialog v-model="dialogFormVisible2" title="Save Data" width="25%">
      <el-date-picker v-model="savingDate" />
      <template #footer>
        <span class="dialog-footer">
          <el-button @click="dialogFormVisible2 = false">Cancel</el-button>
          <el-button type="primary" :loading="formSubmiting" @click="saveMonthlyData">Submit</el-button>
        </span>
      </template>
    </el-dialog>

    <el-dialog v-model="reportDialog" title="Show Report" width="40%">
      <el-space>
        <el-date-picker v-model="reportDate" type="month" placeholder="Pick a month" />
        <el-button type="danger" @click="printPreviousData('all')">PRINT ALL</el-button>
        <el-button type="info" @click="printPreviousData('hkp')">ONLY HKP</el-button>
      </el-space>
      <el-divider></el-divider>
      <el-descriptions class="margin-top" title="Report" border>
        <el-descriptions-item
          v-for="(d, i) in categoryResult"
          :key="i"
          :label="d.category"
        >{{d.total}}</el-descriptions-item>
      </el-descriptions>
      <template #footer>
        <span class="dialog-footer">
          <el-button @click="reportDialog = false">Cancel</el-button>
          <el-button type="primary" :loading="formSubmiting" @click="getCategoryResult">Reload</el-button>
        </span>
      </template>
    </el-dialog>
  </el-container>
</template>

<script>
import { ref, onBeforeMount, reactive } from "vue";
import { ElMessageBox, ElMessage, ElLoading } from "element-plus";
import {
  Plus,
  Check,
  Printer,
  DocumentCopy,
  Search,
  Setting
} from "@element-plus/icons-vue";
import htmlToPaper from "./plugins/print-html";
import Fuse from "fuse.js";

const BASE_URL = "https://harekrishnasamacar.com/book_hisab";
const fetchOptions = {
  method: "POST",
  headers: {
    Accept: "application/json",
    "Content-Type": "application/json"
  }
};

export default {
  name: "App",
  setup() {
    const tempValue = ref(null);
    const dialogVisible = ref(false);
    const refreshKey = ref(1);
    const column = ref(4);
    const printingData = ref([]);

    const loading = ref(false);
    const searchValue = ref(null);
    const selectedOption = ref({});
    const searchfield = ref();

    const dialogFormVisible = ref(false);
    const formSubmiting = ref(false);

    const form = reactive({});

    const setFormDefaultValue = () => {
      form.value = {
        name: "",
        en_name: "",
        category_id: "1",
        created_at: ""
      };
    };
    setFormDefaultValue();

    const dialogFormVisible2 = ref(false);
    const savingDate = ref(new Date());

    const reportDialog = ref(false);
    const reportDate = ref(new Date());
    const categoryResult = ref();

    const openReportDialog = async () => {
      await getCategoryResult();
      reportDialog.value = true;
    };

    const toggleDialog = () => {
      dialogVisible.value = !dialogVisible.value;
      printData();
    };

    let tableData = ref([]);
    let totalCount = ref([]);

    const refreshTable = () => {
      // location.reload();
      localStorage.removeItem("booksData");
      refreshKey.value += refreshKey.value;
    };

    let fuse = null;

    onBeforeMount(async () => {
      const loading = ElLoading.service({
        lock: true,
        text: "Loading",
        background: "rgba(250, 250, 250, 0.7)"
      });
      const books = await fetch(`${BASE_URL}/getBook.php`);
      const { data, total } = await books.json();
      totalCount.value = total;
      tableData.value = data;
      fuse = new Fuse(tableData.value, {
        // includeScore: true,
        keys: ["name", "en_name"]
      });

      if (localStorage.getItem("booksData")) {
        let previousData = JSON.parse(localStorage.getItem("booksData"));
        tableData.value = tableData.value.map(book => {
          let found = previousData.find(p => p.id === book.id);
          if (found) {
            return {
              ...book,
              amount: found.amount
            };
          }
          return book;
        });
      }
      loading.close();
    });

    const searchBook = (query, cb) => {
      if (query) {
        loading.value = true;
        const result = fuse.search(query);
        loading.value = false;
        cb(result.map(book => book.item));
      }
    };

    const getdata = i => {
      if (tableData.value.length) {
        const count = Math.ceil(tableData.value.length / column.value);
        if (i > 0 && i <= column.value) {
          return tableData.value.slice(count * (i - 1), count * i);
        }
      }
      return [];
    };

    const handleAdd = data => {
      if (tempValue.value) {
        data.amount = Number(data.amount) + tempValue.value;
        tempValue.value = null;
      }
      if (searchValue.value) {
        searchValue.value = "";
        searchfield.value.focus();
      }
    };

    const handleCustomAdd = (num, data) => {
      tempValue.value = num;
      handleAdd(data);
    };

    const addNewBook = () => {
      let book = { ...form };
      // Add Api call to add book to JSON file
      formSubmiting.value = true;
      fetch(`${BASE_URL}/addBook.php`, {
        ...fetchOptions,
        body: JSON.stringify(book)
      })
        .then(res => res.json())
        .then(data => {
          ElMessage({ type: "success", message: data.message });
          formSubmiting.value = false;
          setFormDefaultValue();
          dialogFormVisible.value = false;
          const addedBook = {
            id: data.id,
            name: book.name,
            en_name: book.en_name,
            // sort_key: totalCount,
            amount: 0
          };
          // tableData.value.push(addedBook);
          fuse.add(addedBook);
        })
        .catch(err => {
          ElMessage({ type: "error", message: err.message });
          formSubmiting.value = false;
        });
    };

    const addColumn = () => {
      if (column.value < 8) {
        column.value += 2;
      }
    };

    const removeColumn = () => {
      if (column.value > 2) {
        column.value -= 2;
      }
    };

    const saveMonthlyData = () => {
      formSubmiting.value = true;
      const finalBooksAmount = printingData.value.map(book => ({
        id: book.id,
        date: savingDate.value,
        amount: book.amount
      }));

      fetch(`${BASE_URL}/saveHisab.php`, {
        ...fetchOptions,
        body: JSON.stringify(finalBooksAmount)
      })
        .then(res => res.json())
        .then(data => {
          ElMessage({ type: "success", message: data.message });
          formSubmiting.value = false;
          dialogFormVisible2.value = false;
        })
        .catch(err => {
          formSubmiting.value = false;
          ElMessage({ type: "error", message: err.message });
        });
    };

    const printData = () => {
      printingData.value = tableData.value.filter(r => r.amount);
      saveToLocalStorage(JSON.stringify(printingData.value));
    };

    const printMe = () => htmlToPaper("printable");

    const selectBook = book => (selectedOption.value = book);

    const saveToLocalStorage = data => localStorage.setItem("booksData", data);

    const getCategoryResult = async () => {
      fetch(`${BASE_URL}/getReport.php?date=${reportDate.value.toISOString()}`)
        .then(res => res.json())
        .then(data => (categoryResult.value = data))
        .catch(err => ElMessage({ type: "error", message: err.message }));
    };

    const printPreviousData = async type => {
      try {
        const response = await fetch(
          `${BASE_URL}/getReport.php?type=${type}&date=${reportDate.value.toISOString()}`
        );
        const data = await response.json();
        printingData.value = data;
        dialogVisible.value = true;
      } catch (error) {
        ElMessage({ type: "error", message: err.message });
      }
    };

    return {
      refreshTable,
      totalCount,
      loading,
      selectedOption,
      searchBook,
      searchValue,
      selectBook,
      printMe,
      printData,
      tempValue,
      tableData,
      getdata,

      dialogVisible,

      handleAdd,
      handleCustomAdd,
      toggleDialog,

      printingData,
      refreshKey,
      column,
      addColumn,
      removeColumn,

      dialogFormVisible,
      form,
      addNewBook,
      formSubmiting,
      dialogFormVisible2,
      saveMonthlyData,
      savingDate,
      searchfield,

      // Icons
      Plus,
      Check,
      Printer,
      DocumentCopy,
      Search,
      Setting,

      // Report
      reportDialog,
      reportDate,
      categoryResult,
      getCategoryResult,
      printPreviousData
    };
  }
};
</script>

<style>
body {
  font-family: Avenir, Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
#app {
  text-align: center;
  color: #2c3e50;
}
.btn {
  margin-top: 100px;
}
input[type="number"] {
  -moz-appearance: textfield;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
}
.el-input-group__append {
  padding: 0 10px;
}

.btn-group {
  margin-top: 1rem;
  float: right;
}
@media print {
  .not-print {
    display: none;
  }
  .printable {
    display: block;
  }
}
</style>
